/* General Styles */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    color: #333;
  }
  
  /* Container */
  .compressor-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    margin: 2rem;
    min-height: 100vh;
  }
  
  /* Header */
  .compressor-header {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .compressor-header h1 {
    font-size: 2.5em;
    color: #007bff;
    margin-bottom: 2rem;
  }
  
  .compressor-header p {
    font-size: 1.1em;
    color: #555;
  }
  
  /* Card */
  .compressor-card {
    background-color: #ffffff;
    width: 100%;
    max-width: 600px;
    padding: 1.5rem;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  /* File Input */
  .file-input {
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin: auto;
    cursor: pointer;
  }
  
  /* Slider */
  .slider-container {
    margin: 2rem 0;
  }
  
  .slider {
    width: 100%;
    cursor: pointer;
  }
  
  /* Loading */
  .loading p {
    color: #007bff;
    font-weight: bold;
  }
  
  /* Image Preview */
  .image-preview {
    margin: 1rem 0;
  }
  
  .preview-image {
    max-width: 100%;
    max-height: 300px;
    object-fit: contain;
    border: 1px solid #ddd;
    border-radius: 10px;
    margin: 2rem 0;
  }
  
  /* Download Button */
  .download-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007bff;
    color: #ffffff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .download-button:hover {
    background-color: #0056b3;
  }